import { REMOVE_FROM_CART, ADD_TO_CART, SET_QUANTITY } from "../actions/index";
import { initialState } from "./initialState";

const itemReducer = (state = initialState, action) => {
  console.log("itemReducer active");
  switch (action.type) {
    case ADD_TO_CART:
      //TODO
            // state.cartItems//action.payload.quantity

            let quantity = 0;
            let bool = false; // 장바구니에 포함되 있으면 참
            let addItem = {};
      
            // 장바구니에 몇개의 품목이 있는지 모르기때문에 이전 장바구니(state.cratItems)의 항목만큼
            // for루프를 돌린다.
            for(let i = 0; i < state.cartItems.length; i++){
              // 장바구니에 이미 포함되 있음(state.cartItem[i]은 이전 장바구니의 i번째 목록정보)
              if(state.cartItems[i] === action.payload.itemId){
                // 장비구니에 이미 포함되어 있기때문에 bool값은 참이다
                bool = true;
                // action.payload.itemId는 장바구니에 존재하기 때문에
                // quantity는 기존의에 추가한 갯수에 1을 추가한다.
                quantity = 1 + state.cartItems[i].quantity;
                // 이미 이전 항목에 포함되 있는것을 확인했기 때문에 나머지 항목은 무시한다.
                break;
              }
            }
      
            if(bool){ // 기존 장바구니에 포함 되어 있을시
              addItem = {
                // 기존 항목의 아이디를 추가한다.
                "itemId": action.payload.itemId,
                // 기존항목에서 +1한 수량을 추가한다.
                "quantity": quantity,
              }
            }
      
            else{ // 기존 장바구니에 항목이 없을시
              addItem = {
                // 새로운 항목의 아이디를 추가한다.
                "itemId": action.payload.itemId,
                // 새로운 항목이기 때문에 quantity는 1이다.
                "quantity": 1, 
              }
            }
      
            
            // state : 이전 state, state.cartItems : 이전 카트 항목, addItem : 추가 항목
            return Object.assign({}, state, {
              cartItems: [...state.cartItems, addItem]
            });
    case REMOVE_FROM_CART:
      //TODO
      const delId = action.payload.itemId;
      const delCartItems = state.cartItems.filter((item) => 
        item.itemId !== delId);

        return Object.assign({}, state, {
          cartItems: [...delCartItems]
        });

    case SET_QUANTITY:
      let idx = state.cartItems.findIndex(el => el.itemId === action.payload.itemId)
      //TODO

      const obj = [...state.cartItems];
      console.log(idx, obj);

      if(idx === -1) return state;

      obj[idx].quantity = action.payload.quantity;
      console.log(idx, obj);


      // return {...state, cartItems : obj};
      return Object.assign({}, state, {
        cartItems: [...obj]
      });


    default:
      return state;
  }
}

export default itemReducer;